//
// Mixins - Pagination
// ============================================================================

/// Pagination size
@mixin pagination-size($padding-y, $padding-x, $font-size, $line-height, $pag-border-radius) {
  .page-link {
    padding: $padding-y $padding-x;
    font-size: $font-size;
  }

  .page-item {
    &:first-child {
      .page-link {
        @include border-left-radius($pag-border-radius);
      }
    }

    &:last-child {
      .page-link {
        @include border-right-radius($pag-border-radius);
      }
    }
  }
}
